<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Assign') }} {{ $t('User') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>                        
                    </div>
                    <div class="modal-body">                     
                            <dynamic-form lang="en" :buttons="buttons" :schema="FormSchema" ref="AssignTagForm" :mandatorytext='MandatoryString'>
                                    <template v-slot:tgslot-AssignUser="{ data }">
                                        <div class="row">
                                            <div class="col-lg-6 float-left">
                                                <Field :name="'AssignUser'" v-slot="{ errors }" :rules="{ 'required': true }">
                                                    <multiselect 
                                                        mode="tag"
                                                        :placeholder="$t('Select User')" 
                                                        :multiple="true"                                                     
                                                        class="form-group border-0 p-0 mb-0 mr-1" 
                                                        v-model="UserSelect" 
                                                        :options="TeamMemberData" 
                                                        label="name" 
                                                        track-by="value" 
                                                        :taggable="true"
                                                        id="exampleFormControlSelect1">                                                     
                                                    </multiselect>                                                    
                                                </Field>                                           
                                            </div>                                       
                                            <div class="col-lg-6 float-left"><a type="submit" class="btn btn-success float-left" v-on:click="SaveAssociateUser">Associate User</a></div>
                                        </div>
                                    </template> 
                                    
                                    <template v-slot:tgslot-AssociateUserList="{ data }">
                                        <div class="listing">
                                            <div class="table-responsive table-fix-header float-left" style="max-height:530px;">
                                                <table id="mainTable" class="table table-bordered dt-responsive nowrap mt-0">
                                                    <thead class="thead-bg">
                                                        <tr>                                                                
                                                            <th class="text-center">{{ $t('UserName') }}</th>
                                                            <th class="text-center"><span>{{ $t('AliasName') }}</span></th>
                                                            <th class="text-center"><span>{{ $t('Show')}} {{ $t('AliasName') }}</span></th>
                                                            <th class="text-center"><span>{{ $t('Action') }}</span></th>
                                                        </tr>
                                                    </thead>
                                                    <tbody>        
                                                                                                   
                                                        <tr id="trnorecords" v-if="UserList.length==0">
                                                            <td colspan="12"  class="text-danger text-center no-record">
                                                                {{ $t('NoRecordfound') }}
                                                            </td>
                                                        </tr>                                                                                                  
                                                        <tr v-for="(member,index) in UserList" :key="member.userId">                                                               
                                                            <td class="text-center">
                                                                {{ member.UserName }}
                                                            </td>
                                                            <td class="text-center">
                                                                    <input type="text" :class="{ 'form-control': true}"  v-model="member.is_Alias_Name" />  
                                                            </td>
                                                            <td class="text-center">
                                                                <div class="custom-control custom-checkbox">
                                                                    <input type="checkbox" class="chkAlias checkboxAlias custom-control-input" :checked="member.show_alias_Name=='1'" v-bind:id="'checkboxidd'+member.userId"  v-bind:name="'chkk_'+member.userId" v-on:change="checkboxAlias($event,member)" :class="{ 'form-control': true}">
                                                                    <label class="custom-control-label universal-custom-control-label pt-1" v-bind:for="'checkboxidd'+member.userId"></label>
                                                                </div>
                                                            </td>
                                                            <td class="text-center single-action">
                                                                <a typetitle="Delete" typefor="Delete" :value="member.value" title="Delete" @click="DeAssociateUserWithTeam(member.userId)"><em class="fa fa-trash-o action-icon text-danger" :class="{'disabled': member.is_lead==1}"></em></a>
                                                            </td>                                                            
                                                        </tr>
                                                    </tbody>
                                                </table>
                                            </div>
                                        </div>
                       
                                    </template>
                                                 
                            </dynamic-form>
                       
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService'
export default{
    props:{
        TeamId:{
            type:Number,
            Required:true
        },
        AssigenedUser:{
            type:Object,
            Required:true
        }
    },
    data(){
        return{
            MandatoryString:'Fields marked with an asterisk (*) are mandatory.',
            isLoading:false,
            UserSelect:[],
            UserList:[],           
            FormSchema: [               
                {
                    layoutType: "single",
                    Data: [                       
                        {
                            astype: "SlotField",                                                
                            label: this.$t('Assign User'),
                            name: "AssignUser",
                            mode: "tag",
                            multiple:false,
                            value: "", 
                            config: {                              
                                options: []
                            },                           
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": false, max: 100 },
                        }                                            
                    ]
                },
                {
                    layoutType: "single",
                    group_name:'Associate User List',
                    Data: [                       
                        {
                            astype: "SlotField",
                            name: "AssociateUserList",
                            showLabel: false,
                            value: "",               
                            placeholder: this.$t('Select Status'),
                            validationRules: { "required": false, max: 100 },
                        }                                            
                    ]
                }
                
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",                    
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.OnSubmit,
                },            
                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.Close,
                },
            ],
            TeamMemberData:[]
        }
    },
    created:async function(){ 
        var ths= this;   
        ths.GetUsers(); 
        if(ths.AssigenedUser != null){
            setTimeout(function () {
                    ths.BindAssigenedUsers();
            }, 1000);
        }  
    },
    methods:{
       GetUsers() {  
        var ths = this;
        var url = `id=&moduleName=PROJECT&type=COMPANY_USER_LIST_FOR_COMPANY_SETUP&search=&refId=`;

            DataService.GetDDLData(url).then((response) => { 
                if (response.status === 200 && response.data.DATA && response.data.DATA.length > 0) { 
                
                let allUsers = response.data.DATA;             
                ths.TeamMemberData = []; 
                if (ths.AssigenedUser && ths.AssigenedUser.UserId) { 
                    let userIds = ths.AssigenedUser.UserId ? ths.AssigenedUser.UserId.split(",").map(id => id.trim()): [];
                    allUsers = allUsers.filter(x => !userIds.includes(x.value.toString()));
                }
              
                allUsers.forEach(element => {
                    ths.TeamMemberData.push({
                    value: element.value,
                    name: element.first_value
                    });
                });
                }
            });
        },



        BindAssigenedUsers() {
            var vm = this;
            var data = vm.AssigenedUser;

            let teamLeadIds = data.Team_Lead_Id ? data.Team_Lead_Id.split(",") : [];
            let userIds = data.UserId ? data.UserId.split(",") : [];
            let userNames = data.UserName ? data.UserName.split(",") : [];  
            let Alias_Name = data.is_Alias_Name  ? data.is_Alias_Name.split(",").filter(x => x && x.trim() !== "")  : userNames;          
           // let Alias_Name = data.is_Alias_Name ? data.is_Alias_Name.split(",") : userNames;
            let showaliasName = data.show_alias_Name ? data.show_alias_Name.split(",") : [];

            let NewArray = [];

            userIds.forEach((userId, index) => {
                // Check if already added
                let existing = NewArray.find(u => u.userId === parseInt(userId));

                if (!existing) {
                    NewArray.push({
                        team_id: data.team_id,
                        userId: parseInt(userId),
                        UserName: userNames[index] || '',
                        is_lead: teamLeadIds.includes(userId) ? 1 : 0,
                        is_Alias_Name: Alias_Name[index] || '',
                        show_alias_Name: showaliasName[index] || ''
                    });
                } else {
                    // If user already exists and is a team lead, update is_lead
                    if (teamLeadIds.includes(userId)) {
                        existing.is_lead = 1;
                    }
                }
            });

            vm.UserList = NewArray;
        },
       
        SaveAssociateUser:async function(){ 
            var ths=this;
            ths.isLoading=true; 
            if(ths.UserSelect.length>0){
                var val = "";           
                ths.UserSelect.forEach((itm)=>{                               
                    val += itm.value;
                    val += ',';
                });
                var VarObject = {
                    TeamId:ths.TeamId,                
                    UserId: val
                };
                var objectPostString = JSON.stringify(VarObject);
                DataService.AssociateUserWithTeam(objectPostString).then(function (response) {
                    ths.isLoading=false;
                    if(response.data.result.DATA.length>0){ 
                     if(response.data.result.DATA[0].VALUE=='EXIST') {
                            ths.ShowAlert(ths.$t('AlreadyExists',[ths.$t('TeamMember')]),"warning", true, ths.$t("Alert"));
                        }else{
                            ths.ShowAlert(ths.$t('AddedSuccess',[ths.$t('TeamMember')]), "success", true,  ths.$t("Alert"));
                            ths.$parent.FetchTeamList();
                            ths.Close();
                        }                         
                    }else{
                        ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                    }
                })                    
            }
            else{
                ths.isLoading=false;  
                ths.ShowAlert(ths.$t("PleasSelectAtLeast"), "failure", true, ths.$t('Alert'));              
                return false; 
            }  
        },
        DeAssociateUserWithTeam(userId){ 
            var ths=this;             
            ths.confirmR(ths.$t("ConfirmDelete"), true, (ths.$t("Delete") + "  " + ths.$t("User")), function () { 
                ths.isLoading=true;
                var VarObject = {
                    TeamId:ths.TeamId,                
                    UserId: userId
                };
                var objectPostString = JSON.stringify(VarObject);
                DataService.DeAssociateUserWithTeam(objectPostString).then(function (response) {
                    ths.isLoading=false;
                    if(response.data.result.DATA.length>0){  
                        ths.ShowAlert(ths.$t('RecordUpdatedSuccessfully'), "success", true,  ths.$t("Alert"));
                        ths.$parent.FetchTeamList();
                        ths.Close();
                    }else{
                        ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                    }
                }) 
            })
        },
        Close:function(){
            var vm = this;
            vm.$parent.IsAssignedUser=false;
        },
        checkboxAlias:function(evt,item){
            var vm=this;
            if(evt.target.checked){                   
                vm.UserList = vm.UserList.map(itm =>
                    itm.userId === item.userId ? { ...itm, show_alias_Name: true } : itm
                );            
            }else{
                vm.UserList = vm.UserList.map(itm =>
                    itm.userId === item.userId ? { ...itm, show_alias_Name: false } : itm
                );
            }          
        },
        OnSubmit(){
            var ths= this;
            if(ths.UserList.length>0){
                var VarObject = {
                    TeamId:ths.TeamId, 
                    Type:'User',     
                    UserData:ths.UserList
                };
                var objectPostString = JSON.stringify(VarObject);
                DataService.SaveTeam(objectPostString).then(function (response) {
                    ths.isLoading=false;
                    if(response.data.result.DATA.length>0){  
                        ths.ShowAlert(ths.$t('RecordUpdatedSuccessfully'), "success", true,  ths.$t("Alert"));
                        ths.$parent.FetchTeamList();
                        ths.Close();
                    }else{
                        ths.ShowAlert(ths.$t("UnknownErrorOccur"), "failure", true, ths.$t('Alert'));
                    }
                }) 
            }
            else{
                ths.isLoading=false;  
                ths.ShowAlert(ths.$t("PleasSelectAtLeast"), "failure", true, ths.$t('Alert'));              
                return false; 
            } 
        }       
    }
}
</script>
<style>
    .multiselect{z-index: 9999!important;}
</style>