<template>
    <section>
            <tg-list  
                ref="tgList" :listType="['List']" :IsShowAction="true" :showCheckBox="true"  ModuleName="PROJECT" SubModuleCode="Team"  IdentityColumn="team_id" :HeaderText="$t('Team')"
                :ListData="ListingData" :HeaderData="Headers" :widgets="widgets" :HeaderButtons="Buttons" :TotalRecords="TotalRecords" :RenderRowActionMethod= "renderActions" :ListDataCallBackFunction="FetchTeamList" 
                :SortExp="SortExp"  :SortBy="SortBy" :NorecordfoundText="$t('NoRecordfound')"  v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick" 
                v-on:SortdataButtonClick="sortdata" :SearchFields="leftSearchFields" :LegendArray="LegendArray" v-on:LeftsearchButtonClick="leftCommonSearch"
                v-on:ActionButtonClick="actionButtonClick"> 
                <template v-slot:slotdata="{ data }">
                    <template v-if="data.column.COLUMN_NAME == 'Team_name'">                      
                        <span style="color: dodgerblue;cursor:pointer;" @click="AddTeam(data.row)">{{ data.row.Team_name }}</span>                                                 
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'Module_name'">   
                       <span v-if="data.row.Module_name == null"> N/A</span>
                       <span v-else>{{ data.row.Module_name }}</span>                                                                 
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'Number'">                      
                        <a @click="AssignedUser(data.row.team_id,data.row)" class="text-dark" href="javascript:;"  data-placement="left" v-if="data.row.Number ==null">
                            <em class=" fa fa-user-plus"></em>                                
                        </a> 
                        <a  v-else @click="AssignedUser(data.row.team_id,data.row)" class="text-dark" href="javascript:;">
                            <em class="iconsize fa fa-user"></em>
                            <span class="noti-circle noti-blue">{{ data.row.Number }}</span>                            
                        </a>                        
                    </template>
                    <template v-if="data.column.COLUMN_NAME == 'CountAssociatedProject'">
                        <a   @click="AssociatedProject(data.row.team_id)" class="text-dark" href="javascript:;">
                            <em class="iconsize fa fa-briefcase"></em>
                            <span class="noti-circle noti-blue">{{ data.row.CountAssociatedProject }}</span>                            
                        </a>
                    </template>
                    <!-- <template v-if="data.column.COLUMN_NAME == 'Group_Chat'">
                        <a class="text-dark text-primary" href="javascript:;">
                            <em class="iconsize text-primary fa fa-comments-o"></em>                      
                        </a>
                    </template> -->
                </template>               
            </tg-list> 
            <AddTeam v-if="isAddTeam" :TeamId="TeamId"></AddTeam>
            <AssignedUser v-if="IsAssignedUser" :TeamId="TeamId" :AssigenedUser="AssigenedUser"></AssignedUser>
            <AssociatedProject v-if="IsTeam" :TeamId="TeamId" @CloseAssociatedProject="CloseAssociatedProject"></AssociatedProject>
    </section>
</template>
<script>
import Schema from './TeamListSchema';
import DataService from '../../services/DataService'
import AssignedUser from './AssignedUser.vue'
import AddTeam from './AddTeam.vue'
import AssociatedProject from './AssociatedProject.vue';
export default {
    components:{AddTeam,AssignedUser,AssociatedProject},
    data() {
        return {
            TotalRecords: 0,           
            isLoading: false,
            CompanyId: null,
            PageSize: 10,            
            PageNumber: 1,
            isPaged: 1,
            TotalPages: 0,
            widgets: [],
            TeamId:0,
            LegendArray:[],
            SortBy: '',
            SortExp: '',           
            listActions: Schema.listActions,
            Headers: Schema.TableHeader,            
            IsAssignedUser:false,
            leftSearchFields: Schema.leftsearchSchema,
            Buttons: Schema.buttons,
            ListingData: [],
            searchCondition: '',            
            AssigenedUser:'',
            isAddTeam:false,
            Headers:[{
                        "COLUMN_NAME": "Team_name",
                        "DATA_TYPE": "string",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t('TeamName') ,
                        "DISPLAY_ORDER": 1,
                        settings : {
                            isInSlot: true
                        }
                    },
                    {
                        "COLUMN_NAME": "Team_lead",
                        "DATA_TYPE": "string",
                        "SORTABLE": true,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t('LeadName'),
                        "DISPLAY_ORDER": 2
                    },
                    {
                        "COLUMN_NAME": "Module_name",
                        "DATA_TYPE": "string",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t('moduleName'),
                        "DISPLAY_ORDER": 3,
                        settings : {
                            isInSlot: true
                        }
                    },
                    {
                        "COLUMN_NAME": "SkilsName",
                        "DATA_TYPE": "string",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t('skils'),
                        "DISPLAY_ORDER": 3
                    },
                    {
                        "COLUMN_NAME": "Number",
                        "DATA_TYPE": "string",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t('TeamUsers'),
                        "DISPLAY_ORDER": 3,
                        settings : {
                            isInSlot: true
                        }
                    },
                    {
                        "COLUMN_NAME": "CountAssociatedProject",
                        "DATA_TYPE": "string",
                        "SORTABLE": false,
                        "VISIBLE": true,
                        "DISPLAY_NAME": this.$t('AssociatedProject'),
                        "DISPLAY_ORDER": 3,
                        settings : {
                            isInSlot: true
                        }
                    }
                    // {
                    //     "COLUMN_NAME": "Group_Chat",
                    //     "DATA_TYPE": "string",
                    //     "SORTABLE": false,
                    //     "VISIBLE": true,
                    //     "DISPLAY_NAME": "Group Chat",
                    //     "DISPLAY_ORDER": 3,
                    //     settings : {
                    //         isInSlot: true
                    //     }
                    // }
            ],
            IsTeam:false
        };
    },
    created: async function() {   
        this.CompanyId = this.GetUserInfo.CompanyId;
        this.userId = this.GetUserInfo.ID;
        this.FetchTeamList();
    },
    methods: {  
        leftCommonSearch: function (value) {
            this.PageNumber = value.PageNumber;
            this.isPaged = 1;
            if (typeof value.searchCondition != 'undefined') {
                if (value.searchConditionJson != '') {
                    const fvalue = value.searchConditionJson.split('=')[1];
                    this.fav = fvalue;
                } else {
                    this.searchCondition = value.searchCondition;
                }
            }
            else {
                this.searchCondition = "";
            }
            this.FetchTeamList();
        },      
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {                    
                    case 'Edit':
                        action.isVisible = true;
                        break;  
                    case 'Delete':
                        action.isVisible = true;
                        break;                      
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.FetchTeamList();
        },
        actionButtonClick: function (callbackfunction, item) {
            switch (callbackfunction) {              
                case 'DeleteTeam':
                    this.commonDeleteData(item.team_id);
                    break;
                case 'EditTeam':
                    this.AddTeam(item);
                    break;  
                   
            }
        },
        commonHeaderButtonClick: async function(event) {
            var vm=this;
            switch (event.callbackfunction) {
                case "AddTeam":
                    vm.AddTeam(null);
                    break;                
                case "DeleteTeam":
                    vm.commonDeleteData();
                    break;
            }
        },      
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.SortBy = s;
            if (s === this.SortBy) {
                if (this.SortExp == "") {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
                else if (this.SortExp == "ASC") {
                    this.SortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                }
                else {
                    this.SortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchTeamList();
            $("#" + s + '  span:first-child').attr("class", SortDir);
        },    
        commonDeleteData: function (id) {
            var vm = this;
            var val = "";
            vm.isLoading = true;
            if (id == null) {
                $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                    if (val.length > 0)
                        val += ',';
                    val += $(this).val();
                });
            }
            else {
                val = id;
            }
            if (val != null) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Team")), function () {     
                    var objectPostString = `TeamId=${val}`;            
                    DataService.DeleteTeam(objectPostString).then(function (response) {
                        vm.isLoading = false;                        
                        if (response.data.result.DATA.length>0) {
                            if(response.data.result.DATA[0].VALUE=='TEAM_DELETED'){
                                $("#DeleteMultiple").addClass("disabled");
                                vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Team')]), "success", true, vm.$t("Alert"));
                                vm.FetchTeamList();
                            }
                            else {
                                vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Team')]), "Success", true, vm.$t("Alert"));
                            }
                        }
                        else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Team')]), "Success", true, vm.$t("Alert"));
                        }
                    });
                });
            }
            else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
            }
            vm.isLoading = false;
        }, 
        AddTeam(val){
            this.TeamId=val;
            this.isAddTeam=true;
            setTimeout(function () {
                $("#modalAddTeam").modal("show");
            }, 1000);
        },
        FetchTeamList: async function() { 
            var vm = this;
            vm.isLoading=true;
            var url=`teamname=&leadname=&pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}`;
            DataService.FetchTeamList(url).then((response)=>{
                vm.isLoading=false;
                if(response.data.result.Data.length>0){
                    vm.ListingData=response.data.result.Data;
                    vm.TotalRecords = response.data.result.Data[0].TotalRecord;
                    vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                    vm.CurrentPage = vm.PageNumber;
                    vm.ListingData.forEach(Team => {
                            Vue.set(Team, "isSelected", false); 
                            if(Team.meter_count > 0) {
                                Vue.set(Team, "isCheckBoxDisabled", true); ;
                            }
                            else{
                                Vue.set(Team, "isCheckBoxDisabled", false);
                            }
                        });
                }else{
                    vm.ListingData=[];
                    vm.TotalRecords = 0;
                }
                setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
            })
        },
        AssignedUser:function(val,users){
            this.TeamId=val;
            this.AssigenedUser=users;
            this.IsAssignedUser=true;
        },
        AssociatedProject(teamId){
            var ths=this;
            ths.TeamId=teamId;
            ths.IsTeam=true;
        },
        CloseAssociatedProject(){
            var ths=this;
            ths.IsTeam=false;
        }
    }
};
</script>
<style>
.modal-open{overflow: inherit !important}
.iconsize{font-size: 21px;}
</style>