<template>
    <div class="my-popups">
        <loader :is-visible="isLoading"></loader>
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 class="modal-title">{{ $t('Associated') }} {{ $t('Project') }}</h4>
                        <button type="button" class="close" v-on:click="Close">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="modal-body">
                        <!-- Debug JSON output -->
                       

                        <div class="text-center text-danger p-2" v-if="ActivityData.length === 0"> 
                            No record(s) found 
                        </div>

                        <div v-else class="card mb-3 probx">                           
                            <div class="card-body">
                                <div class="row">
                                    <div class="col-md-12 mbspc">                                        
                                        <div class="activity-tab">                                           
                                            <div v-for="(childItem, childIndex) in ActivityData" :key="childIndex" class="card">
                                                <div class="card-header">
                                                    <a data-toggle="collapse" :href="'#collapseOnes' + childIndex"
                                                       style="color: rgb(0, 0, 0);" class="card-link"
                                                       aria-expanded="true">
                                                        <div class="conversation-date heading-border">
                                                            <span>
                                                                <i aria-hidden="true" class="fa fa-angle-down rotate-icon pr-1"></i>
                                                                {{ childItem.project_name }}
                                                            </span>
                                                        </div>
                                                    </a>
                                                </div>

                                                <div :id="'collapseOnes' + childIndex"
                                                     :class="{ 'collapse': true, 'show': childIndex === 0 || (childIndex >= 0 && isCollapsed) }">
                                                    <div class="card-body">
                                                        <div class="row" style="max-height: 200px; overflow-y: auto;">
                                                            <div class="col-md-12 mbspc">
                                                                <!-- <div class="text-center text-danger p-2" v-if="childItem.taskname.length === 0"> 
                                                                    No record(s) found 
                                                                </div> -->

                                                                <div class="row" v-for="(item,index) in childItem.taskname" :key="index">
                                                                    <div class="col-md-3 col-lg-3 ">                                                                        
                                                                        <span class="iconplus">
                                                                            <i class="fa fa-check-circle"></i>
                                                                        </span>
                                                                        <span class="circle-oicon">
                                                                            <i class="fa fa-list"></i>
                                                                        </span>
                                                                        {{ TruncateStr(item.task_name,25) }}
                                                                        
                                                                    </div>                                                                 
                                                                    <div class="col-md-3 col-lg-3 ">
                                                                        <div class="cont_start">
                                                                            <em class="fa fa-user mr-2"></em>
                                                                            <span class="blue-time">
                                                                                {{ Array.isArray(item.username) && item.username.length ? item.username[0].name : '-' }}
                                                                            </span>                                                                        
                                                                        </div>
                                                                    </div>
                                                                    <div class="col-md-2 col-lg-2 ">
                                                                        <div class="form-group">
                                                                            <label>
                                                                                <b>{{ $t('PlanStartDate') }}:</b>
                                                                                {{ $options.filters.formatDate(item.planned_start_date) }}
                                                                            </label>
                                                                        </div>
                                                                    </div>
                                                                    <div class="col-md-2 col-lg-2 ">
                                                                        <div class="form-group">
                                                                            <label>
                                                                                <b>{{ $t('PlanEndDate') }}:</b>
                                                                                {{ $options.filters.formatDate(item.planned_end_date) }}
                                                                            </label>
                                                                        </div>
                                                                    </div>


                                                                    <div class="col-md-2 col-lg-2  float-right">
                                                                        <span class="text-center text-white d-block"  v-if="item.STATUS_NAME != undefined "
                                                                              :style="{ backgroundColor: item.status_bg_color }"> 
                                                                              {{  item.STATUS_NAME}} 
                                                                        </span>
                                                                        <span class="text-center d-block"  v-else>N/A</span>
                                                                    </div>


                                                                </div>

                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>

                                            </div>
                                        </div>
                                    </div>
                                </div>                                
                            </div>                            
                        </div>

                        <div class="row pt-2">
                            <div class="col-6"></div>
                            <div class="text-end text-right col-6">                                       
                                <button type="button" class="btn btn-danger" style="margin-left: 5px;" @click="Close">
                                    <i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}
                                </button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../services/DataService'
export default {
    props:{
        TeamId:{
            type:Number,
            required:true
        }
    },
    data(){
        return{
            isLoading:false,
            ActivityData:[],
            isCollapsed:false
        }
    },
    created(){
        if(this.TeamId > 0){
            this.GetProjectDetails(this.TeamId);
        } 
    },
    methods:{
        GetProjectDetails(teamId){ 
            var vm = this;           
            var url = `TeamId=${teamId}`;
            vm.isLoading = true;
            DataService.GetProjectDetails(url).then((response)=>{
                vm.isLoading = false;

                let rawData = response.data.result.DATA;

                // If backend sends JSON string, parse it
                if (Array.isArray(rawData)) {
                    vm.ActivityData = rawData;
                }
                // Case 2: Object with nested Data property
                else if (rawData && Array.isArray(rawData.Data)) {
                    vm.ActivityData = rawData.Data;
                }
                // Fallback: empty array
                else {
                    vm.ActivityData = [];
                }
            }).catch(()=>{
                vm.isLoading = false;
                vm.ActivityData = [];
            })
        },
        Close(){
            this.$emit('CloseAssociatedProject')
        }
    }
}
</script>
